<template>
    <div class="data-page">
        <el-button class="close fl" type="warning" @click="close">关闭页面</el-button>
        <el-table :data="dataList" border style="width: 100%">
            <el-table-column align-header="center" width="180" align="center" prop="url" label="推广链接"></el-table-column>
            <el-table-column align-header="center" align="center" prop="qudao" label="渠道名称" width="100"></el-table-column>
            <el-table-column align-header="center" align="center" prop="platform" label="小说平台" width="100"></el-table-column>
            <el-table-column align-header="center" align="center" prop="fuwuhao" label="服务号名称" width="100"></el-table-column>
            <el-table-column align-header="center" align="center" prop="pv" label="pv"></el-table-column>
            <el-table-column align-header="center" align="center" prop="uv" label="uv"></el-table-column>
            <el-table-column align-header="center" align="center" prop="count_order" label="订单数量"></el-table-column>
            <el-table-column align-header="center" align="center" prop="count_pay" label="付款数量"></el-table-column>
            <el-table-column align-header="center" align="center" prop="money" label="累计充值金额"></el-table-column>
            <el-table-column align-header="center" align="center" prop="cost" label="成本"></el-table-column>
            <el-table-column align-header="center" align="center" prop="back" label="回本率"></el-table-column>
        </el-table>
    </div>
</template>

<script>
import Axios from 'axios';
export default {
    props: {
        dataList: Array
    },
    data() {
        return {
        }
    },
    methods: {
        close() {
            this.$emit('changeState')
        }
    }
}
</script>

<style scoped>
.close {
    margin-bottom: 30px;
}
</style>



