<template>
    <div class="index-page">
        <div class="links" v-show="!createLink && !createPay && !dataShow">
            <el-button class="create-link fr" type="success" @click="addLink">新建统计链接</el-button>
            <el-table :data="statisticsLinks" border style="width: 100%">
                <el-table-column align-header="center" align="center" prop="createtime" label="日期" width="100"></el-table-column>
                <el-table-column align-header="center" align="center" prop="qudao" label="渠道名称" width="100"></el-table-column>
                <el-table-column align-header="center" align="center" prop="fuwuhao" label="服务号名称" width="100"></el-table-column>
                <el-table-column align-header="center" align="center" prop="platform" label="小说平台" width="100">
                    <template slot-scope="scope">
                        {{scope.row.platform == 0 ? '推锐' : (scope.row.platform == 1 ? '点锐' : (scope.row.platform == 2 ? '要琪' : (scope.row.platform == 3 ? '一飞' : (scope.row.platform == 4 ? '无忧' : (scope.row.platform == 5 ? '有盟' : '袋鼠')))))}}
                    </template>
                </el-table-column>
                <el-table-column align-header="center" align="center" prop="url" label="推广链接"></el-table-column>
                <el-table-column align-header="center" align="center" prop="out_url" label="落地页链接"></el-table-column>
                <el-table-column align-header="center" align="center" label="操作" width="300">
                    <template slot-scope="scope">
                        <el-button @click="editPay(scope.row)" type="warning" size="small">输入成本</el-button>
                        <el-button @click="viewData(scope.row)" type="primary" size="small">查看统计数据</el-button>
                        <el-button @click="deleteLink(scope.row, scope.$index)" type="danger" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="create-page" v-show="createLink">
            <el-form :model="linkForm" label-width="160px" size="small">
                <el-form-item label="创建统计链接的时间">
                    <el-date-picker @change="changeLinkDate" v-model="linkForm.createtime" type="datetime" placeholder="选择日期时间"></el-date-picker>
                </el-form-item>
                <el-form-item label="选择小说平台">
                    <el-select v-model="linkForm.platform" placeholder="请选择">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="小说平台的推广链接">
                    <el-input class="inputtxt" v-model="linkForm.url"></el-input>
                </el-form-item>
                <el-form-item label="微信外的落地页链接">
                    <el-input class="inputtxt" v-model="linkForm.out_url"></el-input>
                </el-form-item>
                <el-form-item label="渠道名称">
                    <el-input class="inputtxt" v-model="linkForm.qudao"></el-input>
                </el-form-item>
                <el-form-item label="服务号名称">
                    <el-input class="inputtxt" v-model="linkForm.fuwuhao"></el-input>
                </el-form-item>
            </el-form>
            <div class="btn-container">
                <el-button type="success" @click="submitLinkForm">提交</el-button>
                <el-button type="info"  @click="cancelLinkForm">取消</el-button>
            </div>
        </div>
        <div class="pay-page create-page" v-show="createPay">
            <el-form :model="payForm" label-width="160px" size="small">
                <el-form-item label="推广链接(不用填)">
                    <el-input disabled class="inputtxt" v-model="payForm.url"></el-input>
                </el-form-item>
                <el-form-item label="总成本">
                    <el-input class="inputtxt" v-model="payForm.cost"></el-input>
                </el-form-item>
                <el-form-item label="记录成本时间">
                    <el-date-picker @change="changePayDate" v-model="payForm.createtime" type="datetime" placeholder="选择日期时间"></el-date-picker>
                </el-form-item>
            </el-form>
            <div class="btn-container">
                <el-button type="success" @click="submitPayForm">提交</el-button>
                <el-button type="info"  @click="cancelPayForm">取消</el-button>
            </div>
        </div>
        <div class="data-page" v-show="dataShow">
            <data-page @changeState="changeState" :dataList="dataList"></data-page>
        </div>
    </div>
</template>

<script>
import Axios from 'axios';
import dataPage from '@/components/dataPage.vue';
export default {
    components: {
        dataPage
    },
    data() {
        return {
            statisticsLinks: [],
            linkForm: {},
            payForm: {},
            createLink: false,
            createPay: false,
            dataShow: false,
            options: [
                {
                    value: 6,
                    label: '袋鼠'
                }
            ],
            dataList: [{
                qudao: '11212'
            }]
        }
    },
    mounted() {
        this.linksShow()
    },
    methods: {
        linksShow() {
            Axios({
                url: '/link',
                method: 'get'
            }).then(res => {
                this.statisticsLinks = res.data.data
            })
        },
        addLink() {
            this.createLink = true
        },
        editPay(row) {
            this.createPay = true
            this.payForm.url = row.url
        },
        viewData(row) {
            Axios({
                url: '/tongji',
                method: 'get',
                params: {
                    url: row.url
                }
            }).then(res => {
                this.dataList = res.data.data
                this.dataShow = true
            })
        },
        deleteLink(row, index) {
            Axios({
                url: '/link/del',
                method: 'get',
                params: {
                    id: row._id
                }
            }).then(res => {
                if(res.data.success) {
                    this.$message({
                        type: 'success',
                        message: res.data.success
                    })
                    this.statisticsLinks.splice(index, 1)
                }
            })
        },
        submitLinkForm() {
            Axios({
                url: '/link/create',
                method: 'post',
                data: this.linkForm
            }).then(res => {
                console.log(res.data)
                if(res.data.success) {
                    this.$message({
                        type: 'success',
                        message: res.data.success
                    })
                    this.linkForm = {}
                    this.statisticsLinks.push(res.data.data)
                    this.createLink = false
                }
            })
        },
        cancelLinkForm() {
            this.linkForm = {}
            this.createLink = false
        },
        changeLinkDate() {
            var date = new Date(this.linkForm.createtime).getTime();
            this.linkForm.createtime = date
        },
        changePayDate() {
            var date = new Date(this.payForm.createtime).getTime();
            this.payForm.createtime = date
        },
        submitPayForm() {
            var time = this.timestamp_date(this.payForm.createtime)
            this.payForm.createtime = time
            Axios({
                url: '/cost/create',
                method: 'post',
                data: this.payForm
            }).then(res => {
                if(res.data.success) {
                    this.$message({
                        type: 'success',
                        message: res.data.data
                    })
                    this.payForm = {}
                    this.createPay = false
                }
            })
        },
        cancelPayForm() {
            this.payForm = {}
            this.createPay = false
        },
        timestamp_date(d) {
            var date = new Date(d)   
            var ms = date.getMinutes()
            var set_ms = 15 * parseInt(ms / 15)
            return date.setMinutes(set_ms, 0, 0)
        },
        changeState() {
            this.dataShow = false
        }
    }
}
</script>


<style scoped>
    .create-link {
        margin-bottom: 30px;
    }
    .create-page {
        width: 400px;
        margin: auto;
        padding: 30px;
        border: 1px solid #eee;
    }
</style>



